import React, { useState, useEffect } from 'react';
import linkedinIcon from '../../assets/icons/socials/linkedin.svg';
import playIcon from '../../assets/icons/socials/play.svg';
import metaIcon from '../../assets/icons/socials/meta.svg';
import telegramIcon from '../../assets/icons/socials/tg.svg';
import twitterIcon from '../../assets/icons/socials/twitter.svg';
import tiktokIcon from '../../assets/icons/socials/tiktok.svg';

import './_Promo.scss';

const Promo = ({ handleScrolling }) => {
    const [scrollPosition, setScrollPosition] = useState(0);

    const handleScroll = () => {
        const position = window.scrollY;
        setScrollPosition(position);
    };

    useEffect(() => {
        window.addEventListener('scroll', handleScroll, { passive: true });

        return () => {
            window.removeEventListener('scroll', handleScroll);
        };
    }, []);

    const getTextColor = (index) => {
        const maxScroll = 300;
        const scrollRatio = Math.min((scrollPosition - index * 50) / maxScroll, 1);
        const grayColor = 156;
        const blackColor = 10;
        const colorValue = Math.floor(grayColor - (grayColor - blackColor) * scrollRatio);
        return `rgb(${colorValue}, ${colorValue}, ${colorValue})`;
    };

    return (
        <>
            <main>
                <section className="media-icons">
                    <div className="circle circle-0"></div>
                    <div className="circle circle-1">
                        <img className='icon linkedin' src={linkedinIcon} alt="LinkedIn" />
                        <img className='icon meta' src={metaIcon} alt="Meta" />
                        <img className='icon telegram' src={telegramIcon} alt="Telegram" />
                    </div>
                    <div className="circle circle-2">
                        <img className='icon play' src={playIcon} alt="Play" />
                        <img className='icon tiktok' src={tiktokIcon} alt="TikTok" />
                    </div>
                    <div className="circle circle-3">
                        <img className='icon twitter' src={twitterIcon} alt="Twitter" />
                    </div>
                </section>
                <div className="container">
                    <section className="promo">
                        <div className="promo__bg"></div>
                        <div className="promo__subtitle">
                            Welcome to Leads Tunnel
                        </div>
                        <h1 className='promo__title'>Drive Your Business Forward with Expert Marketing Solutions</h1>
                        <div className="promo__buttons">
                            <button className='btn btn__contact' onClick={handleScrolling}>Contact US</button>
                        </div>
                    </section>
                </div>
                <section id="show-more" className='show-more' >
                    <span style={{ color: getTextColor(0) }}>
                        At Leads Tunnel , we specialize in crafting innovative marketing strategies tailored to                     </span>
                    <span style={{ color: getTextColor(1) }}>
                        your unique business needs. With a team of seasoned professionals, we're dedicated to
                    </span>
                    <span style={{ color: getTextColor(2) }}>
                        helping you achieve your goals and exceed your expectations.
                    </span>
                </section>
            </main>

        </>
    );
};

export default Promo;
