// В React-компоненте Contact.js

import React, { useEffect, useState } from 'react';
import planet from '../../assets/icons/planet.svg';
import './_Contact.scss';

const Contact = () => {
    const [popup, setPopup] = useState({ show: false, status: '', title: '', subtitle: '', btnText: '' });
    const [isLoading, setIsLoading] = useState(false);
    const [emailValue, setEmailValue] = useState('');

    useEffect(() => {
        if (popup.show) {
            document.body.classList.add('no-scroll');
        } else {
            document.body.classList.remove('no-scroll');
        }
    }, [popup.show]);

    const handleSubmit = async (e) => {
        e.preventDefault();
        const email = e.target.email.value;

        const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
        if (!emailRegex.test(email)) {
            setPopup({
                show: true,
                status: 'error',
                title: 'Invalid Email',
                subtitle: 'Please enter a valid email address.',
                btnText: 'Close'
            });
            return;
        }

        setIsLoading(true);

        try {
            console.log('Sending email to server:', email);
            const response = await fetch('/api/send-email', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify({ email })
            });
            const data = await response.json();
            console.log('Server response:', data);
            setIsLoading(false);
            if (response.ok) {
                setPopup({
                    show: true,
                    status: 'success',
                    title: 'Success',
                    subtitle: 'Your e-mail has been sent successfully!',
                    btnText: 'Close'
                });
                setEmailValue('');
            } else {
                throw new Error(data.message || 'Something went wrong.');
            }
        } catch (error) {
            console.error('Error sending email:', error);
            setIsLoading(false);
            setPopup({
                show: true,
                status: 'error',
                title: 'Oops!..',
                subtitle: 'Something went wrong. Please try again.',
                btnText: 'Try Again'
            });
        }
    };

    const closePopup = () => {
        setPopup({ show: false, status: '', title: '', subtitle: '', btnText: '' });
    };

    const handlePopupClick = (e) => {
        if (e.target.classList.contains('popup')) {
            closePopup();
        }
    };

    const handleEmailChange = (e) => {
        setEmailValue(e.target.value);
    };

    return (
        <>
            <div className="container">
                <div className='contact' id='contact'>
                    <div className="contact__form">
                        <div className="contact__form_title">Ready to take your business <br /> to the next level? </div>
                        <div className="contact__form_descr">Contact us today to schedule a consultation with one of our experts. Whether you're a small startup or a large enterprise, we have the expertise and resources to help you succeed.</div>

                        <form id="emailForm" onSubmit={handleSubmit}>
                            <input type="email" id="email" name="email" required placeholder='E-mail' value={emailValue} onChange={handleEmailChange} />
                            <button type='submit' className="contact__form_btn btn btn__contact" disabled={isLoading}>
                                {isLoading ? 'Sending...' : 'Send'}
                            </button>
                        </form>
                    </div>
                    <div className="contact__img">
                        <img src={planet} alt="planet" />
                    </div>
                </div>
            </div>

            {popup.show && (
                <div className="popup" onClick={handlePopupClick}>
                    <div className="popup__content">
                        <div className='close' onClick={closePopup}></div>
                        <div className="popup__wrapper">
                            <div className='popup__title'>{popup.title}</div>
                            <p className='popup__subtitle'>{popup.subtitle}</p>
                            <button className='btn btn__contact popup__btn' onClick={closePopup}>{popup.btnText}</button>
                        </div>
                    </div>
                </div>
            )}
        </>
    );
};

export default Contact;
